
.message {
    display: flex;
    align-items: flex-end;

    margin: 10px;
    color: cornflowerblue;

}

.avatar {
    height: 100px;
    width: 100px;
    border-radius: 50%;
}

.angle {
    height: 15px;
    width: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, rgba(255, 255, 255, 0.3) 0);

}

.content {
    padding: 10px;
    border-radius: 5px 5px 5px 0;
    background-color: rgba(255, 255, 255, 0.3);
}

.name {
    font-size: 20px;
    font-weight: bold;
}

.textMessage {
    min-width: 150px;
    max-width: 300px;
    word-wrap: break-word;
}

.time {
    display: flex;
    justify-content: flex-end;

}